import React, { useCallback } from 'react';
import { WhiteModal } from 'shared/components/Elements';
import { Branch } from '../../../../BusinessManagement/components/BusinessLocations';

type Props = {
	handleModalOpen: (item: Branch | null) => void;
	open: Branch | null;
};

export function BranchViewModal({ handleModalOpen, open }: Props) {
	const closeModal = useCallback(() => handleModalOpen(null), []);

	const { name, country_name, city, address, state_name, phone, zipcode } = open || ({} as Branch);
	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)}>
				<div className="lg:min-w-[400px]">
					<h2 className="mb-4 text-xl font-semibold">Branch Details</h2>
					<div className="branch-view">
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Branch name:</div>
							<div className="branch-view__value">{name}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Address:</div>
							<div className="branch-view__value">{address}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">City:</div>
							<div className="branch-view__value">{city}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">State:</div>
							<div className="branch-view__value">{state_name}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Zip code:</div>
							<div className="branch-view__value">{zipcode}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Country:</div>
							<div className="branch-view__value">{country_name}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Phone number:</div>
							<div className="branch-view__value">{phone}</div>
						</div>
						<div className="branch-view__item mb-2 flex items-center text-sm">
							<div className="branch-view__field mr-2 font-medium ">Location:</div>
							<div className="branch-view__value">Location map</div>
						</div>
					</div>
				</div>
			</WhiteModal>
		</div>
	);
}
